<template>
  <div class="container">
    <div class="logo">
      <img src="@/assets/weight_control_logo.png" alt="">
    </div>
    <form @submit.prevent="submitHandler" class="form">
      <h2 class="caption">Регистрация</h2>
      <div class="inputs__wrap">
        <span>Email</span>
        <input
            type="text"
            v-model.trim="email"
            class="inputs"
            :class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
            placeholder="email">
        <small
            v-if="$v.email.$dirty && !$v.email.required"
        >
          Поле не должно быть пустым
        </small>
        <small
            v-else-if="$v.email.$dirty && !$v.email.email"
        >
          Введите email
        </small>
      </div>
      <div class="inputs__wrap">
        <span>Пароль</span>
        <input
            type="password"
            v-model.trim="password"
            class="inputs"
            :class="{invalid: ($v.password.$dirty && !$v.password.required) || ($v.password.$dirty && !$v.password.minLength)}"
            placeholder="password">
        <small
            v-if="$v.password.$dirty && !$v.password.required"
        >
          Введите пароль
        </small>
        <small
            v-else-if="$v.password.$dirty && !$v.password.minLength"
        >
          Пароль должен быть не меньше 6 символов
        </small>
      </div>
      <div class="inputs__wrap">
        <span>Имя</span>
        <input
            type="text"
            v-model.trim="name"
            class="inputs"
            :class="{invalid: ($v.name.$dirty && !$v.name.required)}"
            placeholder="name"
        >
        <small
            v-if="$v.name.$dirty && !$v.name.required"
        >
          Введите имя
        </small>
      </div>

      <button type="submit" class="dws-submit">Зарегистрироваться</button>
      <router-link to="/login">Назад</router-link>
    </form>
  </div>

</template>

<script>
import {email, minLength, helpers, required} from 'vuelidate/lib/validators'
const alpha = helpers.regex('alpha', /^[a-zA-Z]+$/)

export default {
  name: "Register",
  data: () => ({
    email: '',
    password: '',
    name: ''
  }),
  validations: {
    email: {
      required,
      email
    },
    password: {
      required,
      minLength: minLength(6)
    },
    name: {
      required
    }
  },
  methods: {
    async submitHandler() {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      let formData = {
        email: this.email,
        password: this.password,
        name: this.name
      }
      try {
        await this.$store.dispatch('register', formData)
        this.$router.push('/set-params')
      } catch (e) {}
    }
  }
}
</script>

